import * as React from "react";
import Header from "components/header";
import Footer from "components/footer";
import ChannelsView from "views/channels";
import GlobalStore from "store/GlobalStore";
import ChannelsStore from "store/ChannelsStore";
import { IS_NODE } from "../../../env";
export default class ChannelListEntry extends React.Component {
    constructor(props) {
        super(props);
        if (!IS_NODE) {
            const { location, match } = props;
            GlobalStore.getInstance({ location, match, cookies: "" });
        }
    }
    render() {
        return (React.createElement("div", null,
            React.createElement(Header, null),
            React.createElement("main", { className: "app-content" },
                React.createElement(ChannelsView, null)),
            React.createElement(Footer, null)));
    }
}
// SSR 在入口组件中获知Store类并初始化用于实例注入
ChannelListEntry.STORE_CLASSES = [GlobalStore, ChannelsStore];



